import { useState } from "react";
import styled from "styled-components";

const Wrap = styled.div`
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  div:first-child {
    font-size: 50px;
    margin-bottom: 20px;
    color: white;
  }
`;

const SearchInput = styled.input`
  width: 400px;
  height: 32px;
  font-size: 15px;
  border: 0;
  border-radius: 15px;
  outline: none;
  padding-left: 10px;
  box-sizing: border-box;
  background-color: #dcdde1;
`;
const SearchBtn = styled.button`
  margin-top: 10px;
  border: 0;
  width: 400px;
  height: 40px;
  font-size: 20px;
  border-radius: 20px;
  background-color: #82c8a0;
  cursor: pointer;
  &:active {
    box-shadow: 0 0 0 2px #cb99c5 inset, 0 0 0 5px rgba(255, 255, 255, 0.15) inset, 0 0 0 3px rgba(0, 0, 0, 0.4);
  }
  &:hover {
    background-color: #82c8d2;
  }
`;

function Home() {
  const [sumName, setSumName] = useState("");

  const searchSummoner = (e) => {
    setSumName(e.target.value);
  };

  const searchBtn = () => {
    if (sumName == "") {
      alert(" !");
    } else {
      window.location.href = `/summoner?name=${sumName}`;
    }
  };
  const enterClick = (e) => {
    if (e.key == "Enter") {
      searchBtn();
    }
  };
  return (
    <>
      <Wrap>
        <div>MY LOL PROFILE</div>
        <SearchInput placeholder="LOL  ." type="text" onChange={searchSummoner} onKeyPress={enterClick} />
        <br />
        <SearchBtn onClick={searchBtn}></SearchBtn>
      </Wrap>
    </>
  );
}
export default Home;
